import {
  TABLE_FAIL,
  TABLE_REQUEST,
  TABLE_SUCCESS,
} from "../Constants/tableConstants";

export const tableReducer = (state = { tabledata: [] }, action) => {
  switch (action.type) {
    case TABLE_REQUEST:
      return { loading: true, tabledata: [] };
    case TABLE_SUCCESS:
      return { loading: false, tabledata: action.payload };
    case TABLE_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
