/* eslint-disable */

import * as React from "react";
import { useState, useEffect, useCallback } from "react"; // Importing useState, useEffect, useCallback

import { useTheme } from "@mui/material/styles";

import Box from "@mui/material/Box";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Chip from "@mui/material/Chip";
import { DemoContainer } from "@mui/x-date-pickers/internals/demo";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { DateRangePicker } from "@mui/x-date-pickers-pro";
import axios from "axios"; // Import axios for making HTTP requests
import { Button } from "@mui/material";

import Chart from "react-apexcharts";

export default function MultipleSelectChip() {
  const theme = useTheme();
  const [selectedNames, setSelectedNames] = React.useState([]);
  const [selectedProducts, setSelectedProducts] = React.useState([]);
  const [siteEnginners, setSiteEngineers] = useState([]);
  const [cartData, setCartData] = useState([]);
  const [siteEngineersName, setSiteEngineersName] = useState([]); // State for storing site engineers names
  const [projectsName, setProjectsName] = useState([]);
  const [load, setLoad] = useState(false);

  //================

  const MyChart = ({ data, selectedSiteEngineers }) => {
    const options = {
      chart: {
        type: "bar",
        height: 350,
      },
      xaxis: {
        categories: selectedSiteEngineers,
        labels: {
          rotate: -45,
          style: {
            fontSize: "13px",
          },
        },
      },
    };

    const series = [
      {
        name: "Commissioned",
        data: data.map((item) => item.data[0]),
      },
      {
        name: "PAT",
        data: data.map((item) => item.data[1]),
      },
      {
        name: "OnAir",
        data: data.map((item) => item.data[2]),
      },
    ];

    return (
      <div id="chart">
        <Chart
          options={options}
          series={series}
          type="bar"
          width={1500}
          height={400}
        />
      </div>
    );
  };

  //================

  const [selectedDateRange, setSelectedDateRange] = React.useState([
    null,
    null,
  ]);

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  };

  // Correct assignment of state variables
  const names = siteEngineersName;
  const products = projectsName;

  function getStyles(name, personName, theme) {
    return {
      fontWeight:
        personName.indexOf(name) === -1
          ? theme.typography.fontWeightRegular
          : theme.typography.fontWeightMedium,
    };
  }

  const handleDateChange = (dateRange) => {
    setSelectedDateRange(dateRange);
  };

  const handleNameChange = (event) => {
    setSelectedNames(event.target.value);
  };

  const handleProductChange = (event) => {
    setSelectedProducts(event.target.value);
  };

  // Memoized getSiteEngineersNames function
  const getSiteEngineersNames = async () => {
    try {
      const res = await axios.get(
        `http://35.78.68.113:80/mpd/api/AllSiteEngineersNamesForMultipleTasksFilter`
      );

      setSiteEngineersName(res.data.siteEngineersNamesArray);
      // console.log(res.data.siteEngineersNamesArray);
    } catch (error) {
      console.error("Error fetching site engineers names:", error);
      // Handle error as needed
    }
  };

  // Memoized getProjectsNames function
  const getProjectsNames = async () => {
    try {
      const res = await axios.get(
        `http://35.78.68.113:80/mpd/api/AllProjectsNamesForMultipleTasksFilter`
      );

      setProjectsName(res.data.ProjectsNamesArray); // Update projectsName state
      // console.log(res.data.ProjectsNamesArray);
    } catch (error) {
      console.error("Error fetching projects names:", error);
      // Handle error as needed
    }
  };

  const fromDate = selectedDateRange[0];
  const toDate = selectedDateRange[1];

  // Function to format date in YYYY-MM-DD format
  const formatDate = (date) => {
    if (!date) return null;
    return date.format("YYYY-MM-DD");
  };

  // Constructing date strings
  const fromDateStr = formatDate(fromDate);
  const toDateStr = formatDate(toDate);

  const getDataForSiteEngineer = async () => {
    const params = {
      selectedSiteEngineer: selectedNames,
      selectedProject: selectedProducts,
      fromDate: fromDateStr,
      toDate: toDateStr,
    };

    try {
      const res = await axios.get(
        "http://35.78.68.113:80/mpd/api/siteEngineerForMonthlyWorkProgressNew",
        {
          params,
        }
      );

      console.log(res.data.FinalDataArray);
      console.log(res.data.selectedSiteEngineer);
      setLoad(true);
      setCartData(res.data.FinalDataArray);
      setSiteEngineers(res.data.firstNamesForSiteEnginners);

      setLoad(false);
    } catch (error) {
      console.error("Error fetching data for site engineer:", error);
      // Handle error as needed
    }
  };

  // useEffect(() => {
  //   getDataForSiteEngineer();
  // }, [selectedDateRange, selectedNames, selectedProducts]);

  useEffect(() => {
    getSiteEngineersNames();
    getProjectsNames();
  }, []); // Update effect dependencies

  const getData = () => {
    console.log("click");
    getDataForSiteEngineer();
  };

  const formattedData = siteEnginners.map((engineer, index) => {
    return {
      name: engineer,
      type: "column",
      data: [
        cartData[0].data[index],
        cartData[1].data[index],
        cartData[2].data[index],
      ],
    };
  });

  return (
    <div>
      <FormControl sx={{ m: 1, width: 650 }}>
        <InputLabel id="demo-multiple-name-chip-label">
          Site Engineer Names
        </InputLabel>
        <Select
          labelId="demo-multiple-name-chip-label"
          id="demo-multiple-name-chip"
          multiple
          value={selectedNames}
          onChange={handleNameChange}
          input={
            <OutlinedInput
              id="select-multiple-name-chip"
              label="Site Engineer Names"
            />
          }
          renderValue={(selected) => (
            <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5 }}>
              {selected.map((value) => (
                <Chip key={value} label={value} />
              ))}
            </Box>
          )}
          MenuProps={MenuProps}
        >
          {names.map((name) => (
            <MenuItem
              key={name}
              value={name}
              style={getStyles(name, selectedNames, theme)}
            >
              {name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      <FormControl sx={{ m: 1, width: 900 }}>
        <InputLabel id="demo-multiple-product-chip-label">Projects</InputLabel>
        <Select
          labelId="demo-multiple-product-chip-label"
          id="demo-multiple-product-chip"
          multiple
          value={selectedProducts}
          onChange={handleProductChange}
          input={
            <OutlinedInput id="select-multiple-product-chip" label="Projects" />
          }
          renderValue={(selected) => (
            <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5 }}>
              {selected.map((value) => (
                <Chip key={value} label={value} />
              ))}
            </Box>
          )}
          MenuProps={MenuProps}
        >
          {products.map((product) => (
            <MenuItem
              key={product}
              value={product}
              style={getStyles(product, selectedProducts, theme)}
            >
              {product}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <Box sx={{ m: 1, width: 700 }}>
        <LocalizationProvider dateAdapter={AdapterDayjs}>
          <DemoContainer components={["DateRangePicker"]}>
            <DateRangePicker
              value={selectedDateRange}
              onChange={handleDateChange}
              localeText={{ start: "Check-in", end: "Check-out" }}
            />
          </DemoContainer>
        </LocalizationProvider>
      </Box>

      <Box sx={{ m: 1, width: 700 }}>
        <Button variant="contained" onClick={getData}>
          conformed
        </Button>
      </Box>

      <Box sx={{ p: 3, pb: 1 }} dir="ltr">
        {load ? (
          <h1>Lodding.. </h1>
        ) : (
          <MyChart data={formattedData} selectedSiteEngineers={siteEnginners} />
        )}
      </Box>
    </div>
  );
}
